.App {
  padding: 0;
  margin: 0;
  overflow: hidden;
  width: 100%;
  color: rgb(23, 23, 23);
}

.button {
  text-align: center;
  padding: 11px 26px 11px 26px;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  border-radius: 10px;
  display: block;
  border: 0px;
  font-weight: 600;
  box-shadow: 0px 0px 14px -7px #f09819;
  background-image: linear-gradient(
    45deg,
    #ff512f 0%,
    #f09819 51%,
    #ff512f 100%
  );
  cursor: pointer;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

@media screen and (max-width: 480px) {
  .button {
    padding: 5px 20px 5px 20px;
    font-size: 16px;
  }
}

.title {
  letter-spacing: 5px;
  font-size: 35px;

  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 90px;
  padding-bottom: 30px;
  font-weight: 800;
  background: linear-gradient(90deg, #e26b2b 1.84%, #f1d1c1 102.67%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@media screen and (max-width: 480px) {
  .title {
    text-align: center;
    padding-bottom: 19px;
    font-size: 30px;
  }
}

@media screen and (max-width: 480px) {
  .title {
    text-align: center;
    padding-bottom: 19px;
    font-size: 30px;
  }
}

.button:hover {
  background-position: right center;

  color: #fff;
  text-decoration: none;
}

.button:active {
  transform: scale(0.95);
}
