.leadmagnet {
    position: fixed;
    bottom: 40px;
    right: 30px;
    margin-left: 20px;
    background-color: #2d6aba;
    padding: 20px;
    border-radius: 40px;
    display: flex;
    align-items: center;
    color: #fff;
    z-index: 3;
    font-weight: 800;
    text-align: center;
    justify-content: center;


    .btn {
        padding: 10px 5px;
        letter-spacing: normal;
        font-size: 12px;

    }
}


.close-button {
    // background-color: #666;

    // padding: 5px 10px;
    // border-radius: 5px;
    // margin-left: 10px;
    cursor: pointer;

    .close {
        font-size: 25px;
        margin-left: 10px;

    }
}

.close-button:hover {
    background-color: #444;
}

@media (max-width: 450px) {
    .leadmagnet {

        width: 90%;
        bottom: 20px;
        right: 0;
        left: 0;


        .l-text {
            display: flex;

            .text {
                font-size: 14px;
                font-weight: 600;

            }
        }
    }
}