.intro {
    display: flex;
    margin-top: 10px;

    .i-left {
        display: flex;
        position: relative;
        flex: 1;
        flex-direction: column;

        .i-name {
            padding-top: 50px;
            display: flex;
            flex-direction: column;
        }

        .i-title {
            width: 80%;
            color: #1c1b1b;
            font-weight: bold;
            font-size: 45px;
            margin-bottom: 10px;
            font-family: 'Montserrat';


        }

        .i-title span {
            color: #fca61f;
            font-weight: bold;
            font-size: 50px;
            margin: 0;
            padding-bottom: 2rem;

        }

        .i-desc {
            font-size: 19px;
            padding-top: 30px;
            width: 60%;
            font-family: 'Montserrat';
            font-weight: 500;
            letter-spacing: 0.7px;
        }

        .i-desc span {
            font-weight: 800;
            font-size: 19px;
            font-family: 'Montserrat';
            color: #f3a323;
            ;
        }

    }

    .i-btn {
        display: flex;
        align-items: center;


        .i-button {
            margin-top: 2.8rem;
            // width: 14rem;
            height: 3rem;
        }
    }

    .i-icons {
        margin-top: 4rem;
        display: flex;
        gap: 20px;
        align-items: center;


        .i-text {
            font-size: 20px;
            font-weight: 400;
        }
    }

    .i-right {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: center;

        .i-img {
            display: flex;
            align-items: center;
            justify-content: center;
        }

    }


    @media screen and (max-width: 1023px) {
        display: flex;
        flex-direction: column-reverse;

        .i-left {
            display: block;
            align-items: center;
            justify-content: center;

            .i-name {
                padding-top: 10px;


                .i-title {
                    font-size: 35px;
                }

                .i-title span {
                    font-size: 35px;
                }

                .i-desc {
                    font-size: 20px;

                }

            }
        }



        .i-right {
            display: flex;
            align-items: center;
            justify-content: center;


            .i-img {
                display: flex;
                align-items: center;
                justify-content: center;


                .i-photo {
                    margin-bottom: 30px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 550px;
                }

            }


        }



    }

    @media screen and (max-width: 767.98px) {

        .i-left {
            display: block;
            flex: 1;

            .i-name {
                width: 560px;

                .i-title {
                    font-size: 40px;
                }

                .i-title span {
                    font-size: 40px;
                }

                .i-desc {
                    width: 500px;
                    font-size: 18px;
                }
            }

            .i-icons {
                margin-bottom: 10px;

                .i-text {
                    font-size: 21px;
                }
            }
        }
    }

    @media screen and (max-width: 597.98px) {

        .i-left {
            display: block;
            flex: 1;

            .i-name {
                width: 390px;

                .i-title {
                    font-size: 40px;
                }

                .i-title span {
                    font-size: 40px;
                }

                .i-desc {
                    width: 370px;
                    font-size: 18px;
                }
            }

            .i-icons {
                margin-bottom: 10px;

                .i-text {
                    font-size: 21px;
                }
            }
        }
    }

    @media screen and (max-width: 480px) {

        .i-left {


            .i-name {
                padding-top: 0px;

                .i-title {

                    font-size: 30px;

                }

                .i-title span {

                    font-size: 30px;


                }

                .i-desc {
                    font-size: 17px;

                    width: 300px;

                }
            }

            .i-btn {
                .i-button {
                    margin-top: 2rem;
                    height: 2.3rem;

                }
            }

            .i-icons {
                margin-top: 2.5rem;
                display: flex;




                .i-text {
                    font-size: 20px;
                    font-family: 'Roboto';
                    font-weight: 400;
                }



            }


        }
    }

    @media screen and (max-width: 429.98px) {

        .i-left {


            .i-name {
                padding-top: 0px;

                .i-title {
                    width: 300px;
                    font-size: 28px;
                    margin: 0;

                }

                .i-title span {

                    font-size: 28px;


                }

                .i-desc {
                    font-size: 17px;

                    width: 280px;

                }
            }



            .i-icons {
                margin-top: 2.5rem;
                display: flex;
                align-items: center;
                justify-content: center;




                .i-text {
                    display: none;

                }



            }


        }
    }

    @media screen and (max-width: 351.98px) {

        .i-left {


            .i-name {
                padding-top: 0px;

                .i-title {
                    width: 250px;
                    font-size: 24px;

                }

                .i-title span {

                    font-size: 24px;


                }

                .i-desc {
                    font-size: 14px;

                    width: 200px;

                }
            }



            .i-icons {
                margin-top: 2.5rem;
                display: flex;
                align-items: center;
                justify-content: center;




                .i-text {
                    display: none;

                }



            }

            .i-button {
                font-size: 14px;
                padding: 10px;
            }
        }
    }


}